const API_KEY = "01df40c9e2cb05c147aa671504b9bf96";
const BASE_PATH = "https://api.themoviedb.org/3";

/*
/(home)  Latest movies, Top Rated Movies  Upcoming Movies  .
/tv  Latest Shows, Airing Today, Popular, Top Rated  .
/search   movie tv    .
/movie/:id    .
/tv/:id  .
*/

interface IMovie {
	id: number;
	backdrop_path: string;
	poster_path: string;
	title: string;
	overview: string;
}

interface ITv {
	id: number;
	backdrop_path: string;
	poster_path: string;
	name: string;
	overview: string;
}

export interface IGetMoviesResult {
	dates: {
		maximum: string;
		minimum: string;
	};
	page: number;
	results: IMovie[];
	total_pages: number;
	total_results: number;
}

export interface IGetTvResult {
	dates: {
		maximum: string;
		minimum: string;
	};
	page: number;
	results: ITv[];
	total_pages: number;
	total_results: number;
}

export function getMovies() {
	return fetch(`${BASE_PATH}/movie/now_playing?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}

export function getTopRatedMovies() {
	return fetch(`${BASE_PATH}/movie/top_rated?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}

export function getUpcomingMovies() {
	return fetch(`${BASE_PATH}/movie/upcoming?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}

export function getLatestTv() {
	return fetch(`${BASE_PATH}/tv/on_the_air?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}

export function getAiringTodayTv() {
	return fetch(`${BASE_PATH}/tv/airing_today?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}
export function getPopularTv() {
	return fetch(`${BASE_PATH}/tv/popular?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}
export function getTopRatedTv() {
	return fetch(`${BASE_PATH}/tv/top_rated?api_key=${API_KEY}`).then(
		(response) => response.json()
	);
}

export function searchTv(keyword: string) {
	return fetch(
		`${BASE_PATH}/search/tv?api_key=${API_KEY}&query=${keyword}`
	).then((response) => response.json());
}

export function searchMovie(keyword: string) {
	return fetch(
		`${BASE_PATH}/search/movie?api_key=${API_KEY}&query=${keyword}`
	).then((response) => response.json());
}
